.main {
    display: flex;
    justify-content: center;
    font-family: sans-serif;
    background-color: #f5f5f5;
    min-height: 100vh;
    user-select: none;
}

.contentSection {
    width: 90%;
}

.title {
    display: flex;
    justify-content: center;
}

h1 {
    font-weight: 100;
    font-size: 60px;
    color: rgba(175, 47, 47, 0.15);
    margin: 0;
}

.inputSection {
    color: #4d4d4d;
    background-color: white;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.inputDiv {
    position: relative;
}

.showSelectAll {
    width: 60px;
    height: 100%;
    text-align: center;
    display: block;
    position: absolute;
    cursor: pointer;
}

.showSelectAll:hover > i {
    border: solid rgba(175, 47, 47, 1);
    border-width: 0 4px 4px 0;
}

.selectAllClicked > i {
    border: solid rgba(175, 47, 47, 1);
    border-width: 0 4px 4px 0;
}

.hideSelectAll {
    display: none;
}

i {
    border: solid #e6e6e6;
    border-width: 0 4px 4px 0;
    display: inline-block;
    padding: 7px;
}

.down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    margin-top: 25%;
}

.noDisplay {
    display: none;
}

ul {
    list-style-type: none;
    padding: 0;
}

.liContent {
    display: flex;
    align-items: center;
    position: relative;
    word-break: break-word;
}

.liContent:hover > .removeItem {
    display: block;
}

li {
    font-size: 18px;
    border-bottom: 1px solid #ededed;
}

.css-checkbox + label, .showSelectAll {
    background: transparent;
    box-shadow: none;
    outline: 0 !important;
    outline: none !important;
    -webkit-tap-highlight-color: transparent !important;
}

/* CheckBox start */

.css-checkbox + label {
    display: block;
    margin: 0 10px;
    cursor: pointer;
}

input[type=checkbox] {
    display: none;
}

.checkBoxNone + label {
    display: none;
}

input[type=checkbox] + label:before {
    content: "\2714";
    border: 0.1em solid rgba(175, 47, 47, 0.15);
    border-radius: 0.2em;
    display: inline-block;
    width: 1em;
    height: 1em;
    padding-left: 0.2em;
    padding-bottom: 0.3em;
    margin-right: 0.2em;
    vertical-align: bottom;
    color: transparent;
    transition: .2s;
}

input[type=checkbox] + label:active:before {
    transform: scale(0);
}

input[type=checkbox]:checked + label:before {
    background-color: MediumSeaGreen;
    border-color: MediumSeaGreen;
    color: #fff;
}

input[type=checkbox]:disabled + label:before {
    transform: scale(1);
    border-color: #aaa;
}

input[type=checkbox]:checked:disabled + label:before {
    transform: scale(1);
    background-color: #bfb;
    border-color: #bfb;
}

/* CheckBox end */

.checked {
    text-decoration: line-through;
    color: #d9d9d9;
    transition: color 0.2s;
    margin: 13px 0;
    /*word-break: break-all;*/
}

.unchecked {
    text-decoration: none;
    transition: color 0.2s;
    margin: 13px 0;
    /*word-break: break-all;*/
}

.editInput {
    width: 100%;
    font-size: 18px;
    padding: 12px 16px;
    margin: 0 0 0 43px;
    outline: none;
    border: 1px solid #999;
    box-shadow: inset 0 -1px 5px 0 rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
}

.removeItem {
    display: none;
    position: absolute;
    right: 10px;
    cursor: pointer;
    border: none;
    background: transparent;
    color: #ff4c4c;
    font-weight: bold;
    font-size: 25px;
    transition: text-shadow 0.2s, color 0.2s;
    outline: none;
}

.removeItemNone {
    display: none;
}

.removeItem:hover {
    color: #b20000;
    text-shadow: -1px 0 #b20000, 0 1px #b20000, 1px 0 #b20000, 0 -1px #b20000;
}

.showFoldingEffect {
    display: block;
}

.hideFoldingEffect {
    display: none;
}

.footerDivLarge, .footerDivSmall {
    height: 4px;
    border-right: 1px solid #bfbfbf;
    border-bottom: 1px solid #bfbfbf;
    border-left: 1px solid #bfbfbf;
    background-color: #f2f2f2;
    margin: 0 auto;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.footerDivLarge {
    border-top: 1px solid #b2b2b2;
    width: 98.5%;
}

.footerDivSmall {
    border-top: 0;
    width: 97.5%;
}


@media only screen and (min-width: 768px) {
    .contentSection {
        width: 80%;
    }

    h1 {
        font-size: 80px;
    }

    li {
        font-size: 22px;
    }

    .editInput {
        font-size: 22px;
    }

    .removeItem {
        font-size: 30px;
    }
}

@media only screen and (min-width: 992px) {
    .contentSection {
        width: 70%;
    }

    h1 {
        font-size: 100px;
    }

    li {
        font-size: 24px;
    }

    .editInput {
        font-size: 24px;
    }

    .removeItem {
        font-size: 35px;
    }
}

@media only screen and (min-width: 1200px) {
    .contentSection {
        width: 60%;
    }
}

